
import VHeader from '@/components/v-header/v-header'
import Scroll from '@/components/scroll/scroll'
import Rater from 'vux/src/components/rater/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import imageTop from '@/assets/img/default_avatar.png'
import uploadFile from '@/api/uploadFile'
import { localUser } from '@/assets/js/local'
import axios from '@/api/axiosApi'
export default {
props: {
fontColor: {
type: String,
default: '#fff'
}
},
data () {
return {
styles: {},
imageTop: imageTop,
dataStar: 5,
agree: true,
atlas: [],
reconmmenderObject: {
environmentRate: 4,
serviceRate: 4,
facilityRate: 4,
sanitationRate: 4
},
show: false
}
},
mounted () {
this.styles.height = window.innerHeight - 64 + 'px'
},
computed: {
imageSize () {
return (window.innerWidth - 30) / 3 - 10
}
},
created () {

this.user = localUser.get()
this.reconmmenderObject.recommend = 1
},
methods: {
publishDynamic () {
this.$refs.selectImg.dispatchEvent(new MouseEvent('click'))
},
selectImg (e) {

if (e.target.files.length > 15) {
this.$vux.toast.text('最多只能选15张图片', 'middle')
return
}

uploadFile(this, e.target.files, (data) => {
let temp = []
data.forEach(item => {
temp.push(item.downloadPath)
})
this.atlas = this.atlas.concat(temp)
e.target.value = ''
})
},

ifPublishRecommanded () {
this.show = true
},
publishRecommanded () {
this.reconmmenderObject.userId = this.user.id
this.reconmmenderObject.productId = this.$route.params.productId
this.reconmmenderObject.outTradeNo = sessionStorage.getItem('outTradeNo')
if (this.reconmmenderObject.comment === '' || this.reconmmenderObject.comment == null) {
this.$vux.toast.text('评论内容为空', 'middle')
return
}
if (this.atlas.length > 0) {
for (var i = 0; i < this.atlas.length; i++) {
this.reconmmenderObject.matchImgUrl = this.atlas[i] + ';'
}
}
if (this.agree) {
this.reconmmenderObject.anonymity = 1
} else {
this.reconmmenderObject.anonymity = 0
}
axios.post(this, '/v1/productComment', this.reconmmenderObject, (data) => {
this.$router.push('/user/order')
this.$store.commit('SET_ORDER_TAB', 2)
this.$vux.toast.text('发布评论成功', 'middle')
})
},
back () {
this.$router.go(-1)
},
onCancel () {
},
onConfirm () {
this.publishRecommanded()
}
},
components: {
VHeader,
Scroll,
Rater,
Flexbox,
FlexboxItem,
XTextarea,
Divider,
CheckIcon,
XSwitch,
Confirm
}
}



